

export default{
 	resdata(name,path,data){
	 let datas={action:path,uniIdToken:uni.getStorageInfoSync('uni_id_token'),...data}
	 
		
		
		console.log(datas)
		uni.showLoading()
	let backData=new Promise(function(resolve,reject){
		uniCloud.callFunction({
					    name: name,
					    data: datas,
					    success(res){
							uni.hideLoading()
					        if(res.result.code === 0) {
								console.log(111,res)
					            resolve(res.result)
					        } else {
								console.log(777,res)
								if(res.result.code=='30204'||res.result.code=='30203'){
									uni.reLaunch({
										url:'/pages/login/login'
									})
								}
					            uni.showModal({
					                content: res.result.message,
					                showCancel: false
					            })
					        }
					    },
					    fail(){
							uni.hideLoading()
					        uni.showModal({
					            content: '请求失败，请稍检测网络',
					            showCancel: false
					        })
					    }
					})
	})
			
		
		return backData

	},
	

	
	
}
