const db = uniCloud.database() //代码块为cdb

export default {
	//  查询仓库所有类型
	  getType() {
		return  new Promise((resolve,reject)=>{
			db.collection('warehouse').get()
			.then((res) => {
				// res 为数据库查询结果
				console.log(999, res)
				resolve(res)
			}).catch((err) => {
				console.log(err.code); // 打印错误码
				console.log(err.message); // 打印错误内容
			})
		} )
			 
	}
}



