	const db = uniCloud.database() //代码块为cdb
	
	export default{
		//  查询入库物品所有类型
	async getType(){
		let data=''
			await  db.collection('goods_type').get()
			  .then((res)=>{
			    // res 为数据库查询结果
				console.log(res)
				data= res.result
			  }).catch((err)=>{
			    console.log(err.code); // 打印错误码
			        console.log(err.message); // 打印错误内容
			  })
			  
			  return data
		}
	}
